<template>
  <div class="docs">
    <el-row>
      <el-col :xs="24"
              :sm="24"
              :md="18"
              :lg="18"
              :xl="18"
              class="content">
        <el-row>
          <el-col :xs="24"
                  :sm="24"
                  :md="24"
                  :lg="24"
                  :xl="24">
            <router-view></router-view>
          </el-col>
        </el-row>
        <el-row>
          <el-col :xs="24"
                  :sm="24"
                  :md="24"
                  :lg="24"
                  :xl="24">
            <paging leftNames="BasicUse"
                    rightNames="SizingIcons">
            </paging>
          </el-col>
        </el-row>
      </el-col>
      <el-col :xs="0"
              :sm="0"
              :md="6"
              :lg="6"
              :xl="6"
              class="daohang hidden-xs-only hidden-sm-only">
        <div v-for="(item,index) in docsDaohaoList"
             :key="index"
             class="list-father">
          <h1 class="list-title"> {{item.title}}</h1>
          <a v-for="(item2,index2) in item.list"
             :key="index2+(item2.child).replace(/\s/g,'')"
             :class="[(item2.child).replace(/\s/g,'')==clickActive?'clickActive':'','list-a']"
             @click="toDocsItemDetails(item2.child)">
            {{item2.child}}
            <br />
          </a>
        </div>
      </el-col>
    </el-row>

  </div>
</template>
<script>
export default {
  data () {
    return {
      daohaoList: [],
      clickActive: 'BasicUse'
    }
  },
  created () {
    this.docsDaohaoList = this.i18n.docsDaohaoList
  },
  computed: {
    i18n () {
      return this.$t('docs')
    }
  },
  components: {
  },
  methods: {
    toDocsItemDetails (names) {
      var trimNames = names.replace(/\s/g, '')
      if (this.$route.path.search(trimNames) !== -1) {
        console.log('包含')
        return
      }
      this.clickActive = trimNames
      console.log(trimNames)
      var toRouter = trimNames
      this.$router.push('/docs/' + toRouter)
    }
  },
  watch: {
    $route (to, from) {
      console.log(this.$route.path)
    }

  }
}
</script>
<style lang="less" scoped>
.clickActive {
  color: #339af0 !important;
}
.docs {
  .content {
    padding: 1rem 1rem 1rem 2rem;
    line-height: 1.8;
    position: relative;
    box-sizing: border-box;
  }
  .daohang {
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    font-size: 22px;
    .list-father {
      padding: 0.5rem;
      background-color: #f1f3f5;
      // background-color: red;
      margin: 0.5rem 0.1rem;
      line-height: 2;
      .list-title {
        // background-color: yellowgreen;
      }
      .list-a {
        // background-color: green;
        line-height: 1;
        color: #868e96;
        cursor: pointer;
        &:hover {
          color: #339af0;
        }
      }
    }
  }
}
@media only screen and (max-width: 767px) {
  .docs {
    .content {
      padding: 0;
    }
  }
}
</style>
